.header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 250px;
    // margin-bottom: 25px;
    background-image:url("../../assets/logo2.jpeg" );    
    background-repeat: no-repeat;
    background-position-x: center;
    border-radius: 20px;
    // padding:5px;
    .name{
      display: flex;
      align-self: center;
    }
    .logo-container {
      height: 100%;
      width: 70px;
      padding: 25px;
    }
  
    .options {
      width: 50%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
  
      .option {
        padding: 10px 15px;
        cursor: pointer;
      }
    }
  }
@media(max-width:450){
  .header{
    width:360px;
  }
}