
.footer { 
  clear: both;
  font-family: monospace;
  left: 0;
  bottom: 0;
  margin: 1em 15% 1em 15%;
  height: 7.5%;
  font-size: 18px;
  color: white;
  text-align: center;
  align-items: center;
  border-radius: 1rem;
  padding: 1rem;
  display: flex;
  border-style: groove;
  width: auto;
}
@media(max-width:600){
  .footer{
    margin-bottom: 12px;
    height: 40px;
    
  }
}