.collection-item {
  width: inherit;
  // display: flex;
  // flex-direction: column;
  // height: 350px;
  align-items: center;
  position: relative;
  // display: block;
  background-color: rgb(193,221,216);
  border: 5px;
  margin-bottom: 20px;
  padding: 3px;
  border-radius: 0.5rem;
  .name{
    align-self: center;
  }

  &.completed{
    background-color: rgb(240,159,175);
    .adm-buttons{
      display: flex;
      flex: inherit;
      flex-direction: row-reverse;
    }
  }
  .inline-buttons{
      display: flex;
      margin: 0em 1em 0em 1em;
      flex-direction: row-reverse;
      padding: 25px;
        
  }
  .custom-button{
    // width: 80%;
    // position: absolute;
    top:255px;
    margin: 0em 1em 0em 1em;

  }

  &:hover{
    .image{
      opacity: 0.8;
    }
    .custom-button{
      opacity: 0.85;
      // display: flex;
      .success{
        background-color: green;
      }
    }
  }
  .collection-footer {
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 18px;
    
    .name {
      display: flex;
      flex-direction: column;
      text-align: center;
      align-items: center;
      margin: 1rem;
      width: 90%;
      margin-bottom: 15px;
    }

  }
}
@media(max-width:600px){
      .collection-item{
        display:flex;
        flex-direction: column;
        margin-bottom: 20px;
        
        .image {
          width: 95%;
          height: 95%;
          background-size: cover;
          background-position: center;
          margin-bottom: 5px;
        }
      
        .collection-footer {
          display:contents;
          justify-content: space-between;
          font-size: 14px;
          
          .name {
            width: 90%;
            margin-bottom: 15px;
          }
      
          .price {
            width: 10%;
          }
        }
        &.completed{
          background-color: rgb(240,159,175);
          .inline-buttons{
            display: flex;
            flex-direction: column-reverse;
            padding: 2px;
          }
          .adm-buttons{
            display: flex;
            flex: inherit;
            flex-direction: row-reverse;
          }
          .buttons{
            margin:5px;
          }
        }
      }
}