.sign-up{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 10px;

    .title{
        margin:10px 0;
    }
    
    .buttons{    
        margin-left: 45%;
        display: flex;
        padding:1em;
        .custom-button{
            margin-right: 25px;
        }
    }
}

@media(max-width:1000px){
    .sign-up{
        display:block;
        margin: 10px;
        width:100%;
        .buttons{
            margin:0px;
            display:flex;
            align-self: center;
        }
    }
  }