.custom-button {
    min-width: 165px;
    width: auto;
    height: 50px;
    letter-spacing: 0.5px;
    line-height: 50px;
    padding: 0 35px 0 35px;
    font-size: 15px;
    background-color: black;
    color: white;
    text-transform: uppercase;
    font-weight: bolder;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    border-radius: 15px;

    &:hover {
      background-color: white;
      color: black;
      border: 1px solid black;
    }

    &.google-sign-in{
      background-color:#4285f4;
      color:white;

      &:hover{
        background-color: #357ae8;
        border: none;
      }
    }

    &.inverted{
      background-color: white;
      color:black;
      border: 1px solid black;

      &:hover{
        background-color: black;
        color: white;
        border: none;
      }
    }

    &.success{
      background-color: rgb(240,159,175);
      color:black;
      border: 1px solid black;

      &:hover{
        background-color: darkgreen;
        color: white;
        border: none;
      }
    }

    &.danger{
      background-color:lightcoral;
      color:black;
      border: 1px solid black;

      &:hover{
        background-color: black;
        color: white;
        border: none;
      }
    }
    
    
  }

  